.profile {
  background-color: white;
  border-radius: 6px;
  width: 100%;
  display: inline-block;
}

.profile_name {
  line-height: 18px;
  padding: 19px;
  text-align: center
}

.profile_name .name {
  text-transform: uppercase;
  font-size: 22px;
  font-weight: bold;
}

.profile_name .title {
  font-size: 20px;
  color: #777;
  font-weight: bold;
}

.profile_image {
  margin-top: -10px;
  -webkit-clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
          clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
}
.profile_image img {
  width: 100%;
}
.profile_information {
  top: 0;
  left: 0;
  margin: -44px 10px 30px;
}

.timelineIten_text {
  font-size: 15px !important;
  color: gray;
  font-weight: bold;

}
.timelineIten_text > a {
  text-decoration: none;
}

.timelineIten_text > span {
  color: black;
}

.profile .button_container {
  width: 100%;
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-justify-content: center ;
          justify-content: center ;
  margin-top: 20px;
}


.navbar > a > .icon {
 font-size: 30px;
  vertical-align: auto;
}

.header_right > a > .MuiSvgIcon-root {
