.section_title {
  position: relative;
  display: inline-block;
  padding: 0 3px;
}
.section_title .section_text {
  font-size: 30px;
  font-weight: 600;
  position: relative;
  margin: 0;
}
.section_title span {
  position: absolute;
  height: 7px;
  width: 100%;
  bottom: 0;
  opacity: 0.4;
  left: 0;
  background-color: #ffc500;
  background-color: var(--main-color);
}
